	module.exports = {"data":{"markdownRemark":{"html":"<p>集中了一些文章，准备写《革命后社会中的政府》。这本书如何能写好，还要好好地设计一下，要有新的切入点。革命后社会中的政府是值得研究的课题，不仅中国发展需要这样的研究，从一般意义上的革命后政府的发展看，研究一下对中国和世界其他社会的发展历程的理解也是有好处的。看看如何能写好这样的题材。</p>\n<p>基本想法是从\"社会资源\"的角度来议论，从革命打碎国家机器和重建国家机器的角度来考虑，以及政治系统在革命后社会中的作用来考虑。这些是革命后社会中最为重要的问题。革命后社会是特殊社会，也是人类历史上经常出现的社会，但是这不是一种社会形态，而是一种特殊的社会结构，由革命本身造成。问题是革命后社会如何变成一个有序的社会，建立一种新的秩序，这是革命后社会向稳定社会过渡的最基本的道路。这一点不解决的话，革命后社会就是不稳定的社会。</p>","id":"/home/swain/workspace/political-life/src/pages/zh/0080.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/zh/0080.md","frontmatter":{"title":"4月24日 星期日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/zh/0080.md absPath of file >>> MarkdownRemark"}}

